body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100vh;
  position: relative;
  z-index: 0;
}

#main-front {
  position: absolute;
  width: 100%;
  height: 100px;
  background: transparent;
  top: 0;
  left: 0;
  z-index: 1;
  pointer-events: none;
}

#main {
  display: flex;
  width: 100%;
  height: 100px;
  z-index: 0;
}

#main-back {
  width: 100%;
  height: 100px;
  z-index: -1;
}
