.ui-menu-top, .ui-menu-bottom {
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 0px;
    padding-bottom: 6px;
    z-index: 10;
    pointer-events: auto;
}

.ui-menu-padding {
    flex: auto;
    pointer-events: none;
    z-index: -10;
}

