.menu-container {
    display: flex;
    flex-direction: row;
    /* z-index: 1; */
}

.menu-left {
    flex-grow: 1;
    text-align: left;
    align-items: center;
    justify-content: center;
}

.menu-center {
    flex-grow: 4;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.menu-right {
    flex-grow: 1;
    text-align: right;
    align-items: flex-end;
    justify-content: center;
}

.menu-icon {
    font-size: 1.3em;
    color: white;
    opacity: 0.6;
    padding: 0;
    /* text-align: center; */
    display: inline-block;
    vertical-align:middle;
    justify-items: center;
}

.menu-emoti {
    font-size: 1.5em;
    opacity: 0.9;
    padding-left: 8px;
    padding-right: 8px;
    text-align: center;
    display: inline-block;
    vertical-align: bottom;
    cursor: pointer;
}